import { Injectable } from '@angular/core';
import { Todo } from './todo-model';
import { Http, Response,Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';

@Injectable()
export class TodoService {

  constructor(private http : Http){
  }
  

   saveTodo(todo : Todo): Observable<Todo>{
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    return this.http.post('http://localhost:8080/v1/todo/saveTodo', todo, options ).map((res: Response) => res.json());
   }

   getTodos():Observable<Todo[]>{
      return this.http.get('http://localhost:8080/v1/todo/getTodos').map((res: Response) => res.json());
   }

   toggleCompleted(id : number):Observable<any>{
    return this.http.get('http://localhost:8080/v1/todo/toggleCompleted/'+id).map((res: Response) => res.json());
   }

   deleteTodo(id : number):Observable<any>{
     return this.http.get('http://localhost:8080/v1/todo/deleteTodo/'+id).map((res: Response) => res.json());
    
   }

  

}



