import { Component } from '@angular/core';
import { Todo } from './todo-model';
import { TodoService } from './todo.service';
import { MatTableDataSource } from '@angular/material/table';
import { TodoDataSource } from './todo.datasource';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  todo = new Todo();
  todos : Todo[];
  displayedColumns = ['title', 'description', 'timeCreated', 'completed'];
  dataSource: TodoDataSource;

  constructor(private service : TodoService){}
  
  ngOnInit() {
  
  }

  createTodo(){
    this.service.saveTodo(this.todo).subscribe(dim=>{
        this.service.getTodos().subscribe(todos=>{
            this.dataSource = new TodoDataSource(todos);
        });
    })
  }

  markCompleted(todo : Todo){
    this.service.toggleCompleted(todo.id).subscribe(dim=>{
      this.service.getTodos().subscribe(todos=>{
        this.dataSource = new TodoDataSource(todos);
    });
    });
  }

  deleteTodo(todo : Todo){
    this.service.deleteTodo(todo.id).subscribe(dim=>{
      this.service.getTodos().subscribe(todos=>{
        this.dataSource = new TodoDataSource(todos);
    });
    });
   
  }

 
  
   
}



